import React from "react";

export function Categories({value, onChangeCategory}) {

    const categories = ['', '', '', '', '', '']

    return (
        <div className="categories">
            <ul>
                {categories.map((p, i) => <li
                    key={i}
                    onClick={() => onChangeCategory(i)}
                    className={value === i ? 'active' : ''}>{p}
                </li>)}
            </ul>
        </div>
    )
}